import CApi from "@api";
import { read, utils } from "xlsx";
import { ErrorNotification, SuccesNotification } from "@components";
import { Modal } from "antd";

const { confirm } = Modal;
const showConfirm = () => {};

export const FGetSidebarMenu = async (updateSidebar) => {
  await CApi({
    method: "GET",
    url: "api/sidebar",
  })
    .then((res) => {
      updateSidebar(res.data);
    })
    .catch((err) => {
      if ("statusText" in err.response && "message" in err.response.data) {
        ErrorNotification(err.response.statusText, err.response.data.message);
      }
    });
};
export const FSubmit = async (
  url,
  forms,
  setErrors,
  getData,
  onCancel,
  setIsSubmit
) => {
  setIsSubmit(true);
  await CApi({
    method: forms.id > 0 ? "PUT" : "POST",
    url: forms.id > 0 ? url + "/" + forms.id : url,
    data: forms,
  })
    .then((res) => {
      onCancel();
      SuccesNotification(res.data.status, res.data.message);
      getData();
    })
    .catch((err) => {
      if ("statusText" in err.response && "message" in err.response.data) {
        ErrorNotification(err.response.statusText, err.response.data.message);
      }
      if ("errors" in err.response.data) {
        setErrors(err.response.data.errors);
      }
    })
    .finally(() => {
      setIsSubmit(false);
    });
};
