export const FSubmitMaster = async (
  url,
  forms,
  setErrors,
  setIsForms,
  setIsEmpty,
  setForms,
  getData
) => {
  await CApi({
    method: forms.id > 0 ? "PUT" : "POST",
    url: forms.id > 0 ? url + "/" + forms.id : url,
    data: forms,
  })
    .then((res) => {
      setIsEmpty(true);
      SuccesNotification(res.data.status, res.data.message);
      setForms({});
      setErrors({});
      setIsForms(false);
    })
    .catch((err) => {
      if ("statusText" in err.response && "message" in err.response.data) {
        ErrorNotification(err.response.statusText, err.response.data.message);
      }
      if ("errors" in err.response.data) {
        setErrors(err.response.data.errors);
      }
    })
    .finally(() => {
      getData();
    });
};

export const FRemove = (id, url, getData) => {
  showConfirm(
    confirm({
      title: "Do you want to delete these items?",
      content: "You can't undo this operation",
      onOk() {
        handleRemove(id, url, getData);
      },
      onCancel() {},
    })
  );
};
const handleRemove = async (id, url, getData) => {
  await CApi.delete(url + "/" + id)
    .then((res) => {
      ErrorNotification(res.data.status, res.data.message);
    })
    .catch((err) => {
      let statusText = "statusText" in err.response;
      let message = "message" in err.response.data;
      if (statusText && message) {
        ErrorNotification(err.response.statusText, err.response.data.message);
      }
    })
    .finally(() => {
      getData();
    });
};
export const FRemoveMaster = (
  id,
  url,
  getData,
  setIsForms,
  setIsEmpty,
  setForms
) => {
  showConfirm(
    confirm({
      title: "Do you want to delete these items?",
      content: "You can't undo this operation",
      onOk() {
        handleRemoveMaster(id, url, getData, setIsForms, setIsEmpty, setForms);
      },
      onCancel() {},
    })
  );
};
const handleRemoveMaster = async (
  id,
  url,
  getData,
  setIsForms,
  setIsEmpty,
  setForms
) => {
  await CApi.delete(url + "/" + id)
    .then((res) => {
      ErrorNotification(res.data.status, res.data.message);
      setIsForms(false);
      setIsEmpty(true);
      setForms({});
    })
    .catch((err) => {
      let statusText = "statusText" in err.response;
      let message = "message" in err.response.data;
      if (statusText && message) {
        ErrorNotification(err.response.statusText, err.response.data.message);
      }
    })
    .finally(() => {
      getData();
    });
};

export function FSearch(e, paginate, setPaginate) {
  setPaginate({
    ...paginate,
    [e.target.name]: e.target.value,
  });
}

export function FSizeChange(current, pageSize, paginate, setPaginate) {
  setPaginate({
    ...paginate,
    pageSize: pageSize,
    current_page: current,
  });
}

export const FGetTableData = async (
  setLoading,
  url,
  paginate,
  setData,
  setTotal,
  id = 0
) => {
  setLoading(true);
  await CApi({
    method: "GET",
    url: url,
    params: {
      page: paginate.current_page,
      keyword: paginate.keyword,
      per_page: paginate.pageSize,
      id: id,
    },
  })
    .then((res) => {
      setData(res.data.data);
      setTotal(res.data.meta.total);
    })
    .catch((_err) => {})
    .finally(() => {
      setLoading(false);
    });
};

