export const FGetMe = async (router, setUser) => {
  await CApi({
    method: "get",
    url: "api/me",
  })
    .then((res) => {
      setUser(res.data);
    })
    .catch((err) => {
      if (err.response.status === 401) {
        router.push("/login");
      }
      if ("statusText" in err.response && "message" in err.response.data) {
        ErrorNotification(err.response.statusText, err.response.data.message);
      }
    });
};

export const FGetAccess = async (router, setAccessList, setIsLoading) => {
  setIsLoading(true);
  let path = router.pathname.split("/");
  let urlAccess = "/" + path[1] + "/" + path[2];
  await CApi({
    method: "GET",
    url: "api/check-access",
    params: {
      path: urlAccess != "//undefined" ? urlAccess : "/",
    },
  })
    .then((res) => {
      setAccessList(res.data);
    })
    .catch((err) => {
      if (err.response.status === 401) {
        router.push("/login");
      }
      setAccessList({
        viewAny: false,
        view: false,
        create: false,
        update: false,
        delete: false,
      });
    })
    .finally(() => {
      setIsLoading(false);
    });
};

export const FReadFile = (file, dataToImport) => {
  var reader = new FileReader();

  reader.onload = (e) => {
    var data = e.target.result;
    data = new Uint8Array(data);
    var workbook = read(data, {
      type: "array",
      cellText: false,
      cellDates: true,
    });

    var res = utils
      .sheet_to_json(workbook.Sheets[workbook.SheetNames[0]], { header: 1 })
      .filter((r) => !!r[0]);
    res.splice(0, 2);
    dataToImport(res);
  };

  reader.readAsArrayBuffer(file);
};

export const FImportData = async (
  url,
  data,
  getData,
  setLoading,
  idAttribute
) => {
  setLoading(true);
  await CApi.post(url, { rows: data })
    .then((res) => {
      SuccesNotification(res.data.status, res.data.message);
      getData();
    })
    .catch((err) => {
      if ("statusText" in err.response && "message" in err.response.data) {
        ErrorNotification(err.response.statusText, err.response.data.message);
      }
      setLoading(false);
    })
    .finally(() => {
      document.getElementById(idAttribute).value = "";
    });
};

export const FDownloadTemplate = async (url, nameFile) => {
  await CApi.get(url, {
    responseType: "blob",
  }).then((res) => {
    const url = window.URL.createObjectURL(new Blob([res.data]));
    const link = document.createElement("a");
    link.href = url;
    link.setAttribute("download", nameFile);
    document.body.appendChild(link);
    link.click();
    link.remove();
  });
};

export const FToBase64 = (file) =>
  new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = (error) => reject(error);
  });
export const FOnPickFile = async (file, id, setValue) => {
  await FToBase64(file).then((res) => {
    const name = file.name;
    const type = "data:" + file.type + ";base64,";
    const valueBase64 = res.replaceAll(type, "");
    const mime = file.type;

    setValue(name, type, valueBase64, mime);
  });
  document.getElementById(id).value = "";
};

export const FOnPickImage = (file, setLoading, id, setValue) => {
  setLoading(true);
  FToBase64(file).then((res) => {
    const type = "data:" + file.type + ";base64,";
    const valueBase64 = res.replaceAll(type, "");
    const name = file.name;
    const path = res;
    setValue(name, path, type, valueBase64);
    setLoading(false);
    document.getElementById(id).value = "";
  });
};

export const FGetMoreData = async (
  setLoading,
  url,
  paginate,
  setData,
  setTotal,
  data,
  currentPage,
  setCurrentPage,
  id = 0
) => {
  setLoading(true);
  await CApi({
    method: "GET",
    url: url,
    params: {
      page: currentPage + 1,
      keyword: paginate.keyword,
      per_page: paginate.pageSize,
      id: id,
    },
  })
    .then((res) => {
      const result = data.concat(res.data.data);
      setData(result);
      setTotal(res.data.meta.total);
      setCurrentPage(res.data.meta.current_page);
    })
    .catch((_err) => {})
    .finally(() => {
      setLoading(false);
    });
};
