import { DeleteOutlined, EditOutlined, PlusOutlined } from "@ant-design/icons";
import { Button, Card, Col, Row, Tabs, Typography } from "antd";
import { useRouter } from "next/router";
import Icon from "@ant-design/compatible/lib/icon";
import React, { useEffect, useState } from "react";
import {
  CDetailMenuData,
  CDetailMenuTable,
  CError,
  CInput,
  CLoadingSpinner,
  CModal,
  CSelectIcon,
  CTitle,
} from "@components";
import CApi from "@api";
import { FRemove, FSubmit } from "@function";

const { Title } = Typography;
const DetailMenu = () => {
  const router = useRouter();
  const { id } = router.query;
  const [isLoading, setIsLoading] = useState(true);
  const [isLoadingTable, setIsLoadingTable] = useState(false);
  const [isHaveData, setIsHaveData] = useState(true);
  const [isSubmit, setIsSubmit] = useState(false);
  const [isModal, setIsModal] = useState(false);
  const [listIcons, setListIcons] = useState([]);
  const [errors, setErrors] = useState({});
  const [detail, setDetail] = useState({
    id: "",
    label: "",
    icon: "",
    path: "",
    roles: [],
    children: [],
  });
  const [forms, setForms] = useState({
    id: "",
    label: "",
    icon: "",
    path: "",
    parent_id: id,
  });
  useEffect(() => {
    getData();
    getIconList();
  }, []);
  const getData = () => {
    setIsLoading(true);
    CApi({
      method: "GET",
      url: "api/menu/" + id,
    })
      .then((res) => {
        setDetail(res.data);
      })
      .catch((err) => {
        if (err.response.status == 404) {
          setIsHaveData(false);
          setErrors(err.response);
        }
      })
      .finally(() => {
        setIsLoading(false);
      });
  };

  const getDataRefresh = () => {
    setIsLoadingTable(true);
    CApi({
      method: "GET",
      url: "api/menu/" + id,
    })
      .then((res) => {
        setDetail(res.data);
      })
      .catch((_err) => {})
      .finally(() => {
        setIsLoadingTable(false);
      });
  };
  const getIconList = () => {
    CApi({
      method: "GET",
      url: "api/list-icon",
    })
      .then((res) => {
        setListIcons(res.data);
      })
      .catch((_err) => {});
  };

  const handleEdit = (e) => {
    setIsModal(!isModal);
    setForms({
      id: e.id,
      label: e.label,
      icon: e.icon,
      path: e.path,
      parent_id: id,
    });
  };
  const handleChange = (e) => {
    setForms({
      ...forms,
      [e.target.id]: e.target.value,
    });
  };
  const onIcons = (value) => {
    setForms({
      ...forms,
      icon: value,
    });
  };

  const onCancel = () => {
    setIsModal(!isModal);
    setForms({
      id: "",
      label: "",
      icon: "",
      path: "",
      parent_id: id,
    });
  };

  const columns = [
    {
      title: "Label",
      key: "label",
      dataIndex: "label",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Icon",
      key: "icon",
      align: "center",
      width: 100,
      render: (item) => <Icon type={item.icon} />,
    },
    {
      title: "Path",
      dataIndex: "path",
      key: "path",
      width: 200,
      align: "center",
      ellipsis: true,
    },
    {
      title: "Action",
      key: "id",
      width: 120,
      align: "center",
      fixed: "right",
      render: (item) => (
        <Row justify="center">
          <Col span={8}>
            <Button
              type="primary"
              style={{ backgroundColor: "#FAAD14" }}
              icon={<EditOutlined />}
              size="small"
              onClick={() => {
                handleEdit(item);
              }}
            />
          </Col>
          <Col span={8}>
            <Button
              onClick={() => {
                FRemove(item.id, `/api/menu`, getDataRefresh);
              }}
              type="primary"
              icon={<DeleteOutlined />}
              size="small"
              danger
            />
          </Col>
        </Row>
      ),
    },
  ];
  const handleInsert = (e) => {
    setIsModal(!isModal);
  };
  const items = [
    {
      key: "1",
      label: (
        <div onClick={handleInsert}>
          <PlusOutlined /> Add Menu {this}
        </div>
      ),
    },
  ];

  if (isLoading) {
    return <CLoadingSpinner />;
  }

  if (!isHaveData) {
    return (
      <CError
        status={errors.status}
        subTitle={errors.data.message}
        labelButton="Back"
        handleBack={() => router.push("/developer/menu")}
      />
    );
  }

  return (
    <>
      <CTitle />
      <Card
        cover={
          <Col
            span={24}
            style={{ backgroundColor: "#1890ff", paddingTop: "5px" }}
          />
        }
      >
        <Row>
          <Col span={22}>
            <Title level={3}>Detail Menu {" > " + detail.label}</Title>
          </Col>
          <Col span={2}>
            <Button
              type="primary"
              onClick={() => router.push("/developer/menu")}
              style={{ float: "right" }}
            >
              Back
            </Button>
          </Col>
        </Row>
        <Row gutter={4} style={{ flex: 1 }}>
          <Col span={24}>
            <Tabs
              defaultActiveKey="1"
              items={[
                {
                  label: "Data",
                  key: "Data",
                  children: <CDetailMenuData data={detail} />,
                },
                {
                  label: "Child",
                  key: "Children",
                  children: (
                    <CDetailMenuTable
                      items={items}
                      columns={columns}
                      total={detail.children.length}
                      data={detail.children}
                      isLoadingTable={isLoadingTable}
                      scroll={{ y: 250 }}
                    />
                  ),
                },
              ]}
            />
          </Col>
        </Row>
      </Card>

      <CModal
        id={forms.id}
        title="Menu"
        onCancel={onCancel}
        onSubmit={() =>
          FSubmit(
            "api/menu",
            forms,
            setErrors,
            getDataRefresh,
            onCancel,
            setIsSubmit
          )
        }
        isSubmit={isSubmit}
        open={isModal}
      >
        <CInput
          value={forms.label}
          id="label"
          validate={errors.label}
          isRequired={true}
          label="Label"
          onChange={handleChange}
        />
        <CSelectIcon
          validate={errors.icon}
          value={forms.icon}
          id="icon"
          options={listIcons}
          isRequired={true}
          label="Icon"
          onChange={onIcons}
        />
        <CInput
          value={forms.path}
          validate={errors.path}
          id="path"
          isRequired={true}
          label="Path"
          onChange={handleChange}
        />
      </CModal>
    </>
  );
};

export default DetailMenu;
